
.header {
  background-color: #1abc9c;
  height: 160px;
  margin-bottom: 60px;
  position: relative;
}
.header__menu {
  max-width: 1000px;
  background-color: #fff;
  margin: 90px auto 0;
  height: 120px;
}
.header__link {
  height: 80px;
  width: 80px;
}
.header__listSwitch {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
}


